@import './base.scss';

.container {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.button {
    margin: 0 10px;
    background: transparent;
    border: none;
    font-size: 40px;
    cursor: pointer;

    @media screen and (max-width: 500px) {
        font-size: 30px;
        margin: 0;
    }
    
    svg {
        fill: white;
    }
}
