@import './base.scss';

.podContainer {
    display: flex;
    flex-wrap: wrap;
    max-width: 500px;
    margin: 0 auto;
}

.opponent {
    max-width: 50%;
    overflow: hidden;
    padding: 3px;

    .avatarWrapper {
        max-height: 9vh;
        overflow: hidden;
        display: flex;
        border-radius: 10px;
        position: relative;

        .avatar {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center top;
        }
    }

    .halfFlexWrapper {
        width: 100%;
        height: 100%;
        max-height: 9vh;
        overflow: hidden;
        display: flex;
        border-radius: 10px;

        .halfFlex {
            width: 50%;
            overflow: hidden;

            .avatar {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center top;
                display: block;
            }

            // &.leftBorderRadius {
            //     border-top-left-radius: 10px;
            //     border-bottom-left-radius: 10px;
            // }

            // &.rightBorderRadius {
            //     border-top-right-radius: 10px;
            //     border-bottom-right-radius: 10px;
            // }
        }
    }
}