@import './base.scss';

.cardsContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #000;
    width: 100%;
    padding: 40px 0;
    overflow-x: hidden;

    @media screen and (max-width: 500px) {
        // height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: #000;
        width: 100%;
        overflow-y: auto;
        padding-top: 16px;
    }


    .card {
        width: 50vw;
        height: 70vw;
        transition: transform 0.3s;
        z-index: 1;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
        overflow-x: hidden;

        @media screen and (max-width: 500px) {
            transition: transform 0.3s;
            z-index: 1;
            box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
            overflow: hidden;
            margin-bottom: -60vw;

            &:last-child {
                margin-bottom: 0;
            }
        }

        img.cardImage {
            width: 100%;
            height: auto;
            border-radius: 8px;
        }
    }
}