@import './base.scss';

.inputContainer {
    text-align: center;
    background: #000;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    height: 100%;
}

textArea {
    margin-top: 20vh;
    width: 100%;
    height: 40px;
    padding: 10px;
    margin: 10px;
    border-radius: 5px;
}

.input {
    width: 80%;
    padding: 10px;
    margin: 10px;
    border-radius: 5px;
}

button {
    padding: 10px 20px;
    border: 1px solid #fff;
    border-radius: 5px;
    background-color: #000;
    color: #fff;
    cursor: pointer; 
    text-transform: uppercase;
}