@import './base.scss';

.app {
  height: 100vh;
  background-color: #000;
  position: relative;
  padding: 10px;
  overflow: hidden;


  header {
    width: 100%;
    height: 20vh;
    background-color: #000;
  }

  .main {
    width: 100%;
    height: 80vh;
    overflow-y: auto;

    .wrapper {
      position: relative;
      width: 100%;
      min-height: 100%;
    }
  }

  footer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 15vh;
    z-index: 20;
    background-color: #00000080;
  }
}